/* Existing styles */
.sideAcontent-container {
    text-align: center;
    padding: 20px;
}

.logo {
    width: 150px; /* Adjust the logo size as needed */
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 15px; 
}

h1 {
    margin-bottom: 10px;
}

.star-rating {
    font-size: 24px;
    color: #FFD700; /* Gold color for the stars */
    margin-bottom: 20px;
}

.description {
    font-size: 18px;
    color: #333;
    margin-bottom: 30px;
}

.image-gallery {
    display: flex;
    justify-content: center;
    gap: 10px; /* Adds space between images */
    overflow-x: auto; /* Allows horizontal scrolling if needed */
}

.image-gallery img {
    width: 150px;
    height: 300px;
    object-fit: cover;
    flex-shrink: 0; /* Prevents the images from shrinking */
    border-radius: 15px; 
}

/* Modal styles */
.modal-overlay {
    background-color: rgba(0, 0, 0, 0.6); /* Slightly transparent black background */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000; /* Ensure modal is on top */
}

.modal {
    background: rgba(255, 255, 255, 0.3); /* Slightly transparent white background */
    padding: 20px;
    border-radius: 10px;
    max-width: 90%;
    max-height: 90%;
    position: relative;
    overflow: hidden; /* Prevents content from overflowing */
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3); /* Optional: adds shadow for better visibility */
}

.modal-image {
    max-width: 100%;
    max-height: 100%;
    width: auto; /* Ensures image scales while maintaining aspect ratio */
    height: auto; /* Ensures image scales while maintaining aspect ratio */
}

.close-button, .arrow-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(255, 0, 0, 0); /* Semi-transparent red background */
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 11px;
    border-radius: 5px; /* Optional: to make the button rounded */
    transition: background 0.3s ease; /* Smooth transition for background color */
}

.close-button {
    top: 10px;
    right: 0px;
}

.arrow-button {
    width: 40px;
    height: 40px;
}

.prev-button {
    left: 10px;
}

.next-button {
    right: 10px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .modal {
        max-width: 95%; /* Slightly more margin on mobile */
    }

    .modal-image {
        width: auto;
        height: auto;
    }

    .arrow-button {
        width: 30px;
        height: 30px;
        font-size: 18px;
    }
}

/* Increase size of close and arrow buttons on larger screens */
@media (min-width: 769px) {
    .close-button {
        font-size: 16px;
        padding: 15px;
    }

    .arrow-button {
        width: 50px;
        height: 50px;
        font-size: 24px;
    }
}
